/* &&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&& global &&& */
body.adminhtml-maillog-history-index h3.icon-head, body.adminhtml-maillog-history-view h3.icon-head,
body.adminhtml-maillog-sync-index h3.icon-head {
	float:none; min-height:19px; background:url("../../../images/fam_lorry.gif") no-repeat left center;
}

body.adminhtml-maillog-history-index h3.icon-head span,
body.adminhtml-maillog-sync-index h3.icon-head span { float:right; font-size:12px; font-weight:400; }

body.adminhtml-maillog-history-index h3.icon-head span a,
body.adminhtml-maillog-sync-index h3.icon-head span a { color:inherit; }

body table.data.adminhtml-maillog-history tr.parts td.a-right {
	background:url("../../../images/luigifab/maillog/mail-attachment.svg") no-repeat -7px center;
}

/* &&&&&&&&&&&&&&&&&&&&&&&&&&&&& maillog/history/view &&& */
body.adminhtml-maillog-history-view div.content div { display:table; margin:1.4em auto; width:100%; border-spacing:1em 0; border-collapse:separate; }
body.adminhtml-maillog-history-view div.content ul + ul { width:62%; }

body.adminhtml-maillog-history-view div.content ul {
	display:table-cell; margin:0; padding:0.7em 1em; height:7.2em;
	font-size:13px; border-radius:1em; border:1px solid #DDD; background-color:#EFEFEF; background-clip:padding-box;
}

body.adminhtml-maillog-history-view div.content li strong { height:auto; line-height:16px; }
body.adminhtml-maillog-history-view .maillog-status { display:inline-block; margin:0 0.5em; width:13em; vertical-align:text-bottom; }

body.adminhtml-maillog-history-view div.content iframe {
	display:block; margin:1em auto; width:calc(100% - 2em); min-height:26em; overflow:hidden;
	border-radius:1em; border:1px solid #DDD; color:white;
}

/* &&&&&&&&&&&&&&&&&&&&&&&&&&&&&&& maillog/sync/index &&& */
body.adminhtml-maillog-sync-index table.data div.details {
	margin-top:-1px; column-count:3; -moz-column-count:3; -webkit-column-count:3;
	font-size:10px; line-height:13px;
}

body.adminhtml-maillog-sync-index table.data div.details:last-child { margin-bottom:5px; }

/* &&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&& system/config &&& */
select.maillog option.has { font-weight:700; }

.comment.maillogcomment { padding-bottom:1em; }
.comment.maillogcomment p  { margin-bottom:0; line-height:150%; }
.comment.maillogcomment ul { margin:0.4em 0; }
.comment.maillogcomment li { margin:0; line-height:1.5em; }
.comment.maillogcomment li:before { content:"âž¤\0000A0"; }
.comment.maillogcomment a { margin:0; }

.maillogexamples { display:block; margin:1.5em 0; padding-left:6px; font-size:0.9em; line-height:1.4em; border-left:3px solid #AAA; }
.maillogexamples pre > span { display:block; column-count:3; -moz-column-count:3; -webkit-column-count:3; }

#lifetimeConfig li:hover { background-color:#DEDEDE; }
#lifetimeConfig span { display:inline-block; padding:0 5px; width:197px; text-align:right;}
#lifetimeConfig input { margin-right:5px; width:60px; height:15px; cursor:pointer; }
#lifetimeConfig select { margin-right:5px; width:136px; }

#pictureConfig { margin:10px 0 15px; }
#pictureConfig thead tr.headings th { position:sticky; top:37px; padding:2px 5px; background:url("../../../images/sort_row_bg.gif") 0 50% repeat-x; }
#pictureConfig tr.template { display:none; }
#pictureConfig td.config { padding-bottom:0.5em; }
#pictureConfig input.cde { display:block; margin-top:0.3em; width:95%; }
#pictureConfig input.cmt { display:block; margin-top:0.3em; width:95%; color:gray; }
#pictureConfig .validation-advice { position:absolute; display:none; max-width:20em; background-color:white; }
#pictureConfig .validation-failed { border-color:red; }
#pictureConfig .validation-failed:hover ~ .validation-advice { display:block; }
#pictureConfig .line { display:flex; white-space:nowrap; padding-right:1em; }
#pictureConfig .line:hover { background-color:#DEDEDE; }
#pictureConfig .line input { width:5em; }
#pictureConfig .bp { min-width:12em; }
#pictureConfig .sz { min-width:13em; }
#pictureConfig .rt { min-width:4em; }

#maillog_sync_emarsys {
	image-rendering:optimizeQuality; image-rendering:-moz-crisp-edges;
	background:url("../../../images/luigifab/maillog/logo-emarsys.svg") no-repeat calc(100% - 20px) 15px;
	background-size: auto 30px;
}

#maillog_sync_dolist {
	image-rendering:optimizeQuality; image-rendering:-moz-crisp-edges;
	background:url("../../../images/luigifab/maillog/logo-dolist.svg") no-repeat calc(100% - 20px) 15px;
	background-size: auto 30px;
}

/* &&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&&& statut &&& */
.maillog-status {
	display:block; margin-top:1px; height:auto; line-height:16px; text-align:center;
	font-size:10px; font-weight:700; text-transform:uppercase; color:white; border-radius:10px;
}

.maillog-status.grid-pending { background-color:#B3B4B3; }
.maillog-status.grid-sending { background-color:#FF9C00; }
.maillog-status.grid-running { background-color:#FF9C00; }
.maillog-status.grid-sent    { background-color:#FFC700; }
.maillog-status.grid-success { background-color:#3CB861; }
.maillog-status.grid-read    { background-color:#3CB861; }
.maillog-status.grid-error   { background-color:#E41101; }
.maillog-status.grid-bounce  { background-color:black; }
.maillog-status.grid-notsent { background-color:black; }
.maillog-status.grid-notsync { background-color:black; }

.maillog-status.grid-error, .maillog-status.grid-bounce {
	animation:maillogBlink 1s step-start 0s infinite; -webkit-animation:maillogBlink 1s step-start 0s infinite;
}

@keyframes maillogBlink { 30% { color:transparent; } }
@-webkit-keyframes maillogBlink { 30% { color:transparent; } }